.pkr {
    height: 94vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.game-area {
    width: 80vw;
    height: 80vh;
    background-color: slateblue;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.players {
    display: flex;
    flex-direction: row;
}

.player {
    display: flex;
    flex-direction: column;
    background-color: rgb(40, 63, 87);
    margin: 1rem;
    /* border-radius: 20%; */
    width: 170px;
    height: 250px;
}

.player-img {
    width: 100%;
    background-color: slategray;
}


.pot {
    margin-top: 5rem;
    font-size: 3rem;
}