.t {
	width: 50px;
	height: 25px;
	border-radius: 20px;
	border: 2px solid black;
	background-color: gray;
	position: fixed;
	top: 10px;
	right: 10px;
	z-index: 999;
	display: flex;
	align-items: center;
	justify-content: space-around;
}

.t-icon {
	width: 15px;
	height: 15px;
}

.t-button {
	width: 25px;
	height: 25px;
	border-radius: 50%;
	background-color: white;
	position: absolute;
	cursor: pointer;
	left: 0;
}